.ama__layout--two-column {
  display: grid;
  display: -ms-grid;
  grid-template-columns: 1fr 5fr 3fr;
  grid-template-rows: auto auto auto;
  min-height: 30em;
  -ms-grid-columns: 1fr 5fr 3fr;
  -ms-grid-rows: auto auto auto;

  &__content-top {
    grid-column: 1 / 5;
    grid-row: 1;

    @include breakpoint($bp-small min-width) {
      grid-column: 1 / 5;
      grid-row: 1;
    }

    @include breakpoint($bp-med min-width) {
      grid-column: 1 / 5;
      grid-row: 1;
      -ms-grid-column: 2;
      -ms-grid-row: 1;
    }
  }

  &__page-content {
    grid-column: 1 / 5;
    grid-row: 2;

    @include breakpoint($bp-small min-width) {
      grid-column: 1 / 4;
      grid-row: 2;
    }

    @include breakpoint($bp-med min-width) {
      grid-column: 2;
      grid-row: 2 / 4;
      -ms-grid-column: 2;
      -ms-grid-row: 2;
    }
  }
} // end article

.ama__layout--two-column__sidebar {
  &--primary {
    grid-column: 1 / 5;
    grid-row: 2;
    -ms-grid-row: 2;

    @include breakpoint($bp-med min-width) {
      grid-column: 1;
      grid-row: 1 / 4;
      -ms-grid-column: 1;
      -ms-grid-row: 1;
    }
  } // end aside.primary

  &--secondary {
    grid-column: 1 / 5;
    grid-row: 3 / 4;

    @include breakpoint($bp-med min-width) {
      @include gutter($padding-left-full...);
      grid-column: 3;
      grid-row: 2 / 4;
      -ms-grid-column: 3;
      -ms-grid-row: 2;
    }
  } // end aside.secondary
}

